* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif, Helvetica;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  font-weight: 500;
  line-height: 60px;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.catalog {
  grid-template-columns: 200px;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  margin: auto;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__details {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  display: block;
}

.card__reviews {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews-text {
  align-self: end;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  align-self: center;
  line-height: 18px;
}

.card__price-amount {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}
